<template>
    <form @submit.prevent="submitHandler" class="form">
        <div class="progress" v-if="loader">
            <div class="indeterminate"></div>
        </div>
        <div class="row">
            <div class="col s12 m6">
                <div class="input-field">
                    <select ref="select" multiple>
                        <!-- <option value="" disabled selected>Choose your option</option> -->
                        <option value="1">Option 1</option>
                        <option value="2">Option 2</option>
                        <option value="3">Option 3</option>
                    </select>
                    <label>Materialize Select</label>
                </div>
            </div>
            <div class="col s12 m6">
                <p>
                    <label>
                        <input type="checkbox" value="red" v-model="checkbox"/>
                        <span>Red</span>
                    </label>
                    </p>
                    <p>
                    <label>
                        <input type="checkbox" value="yellow" v-model="checkbox"/>
                        <span>Yellow</span>
                    </label>
                </p>
            </div>
        </div>
        <div class="row">
            <div class="col s12 m6">
                <div class="input-field">
                    <input type="text" class="datepicker" ref="start">
                    <label class="active">Start date</label>
                </div>
            </div>
            <div class="col s12 m6">
                <div class="input-field">
                    <input type="text" class="datepicker" ref="end">
                    <label class="date-active">End date</label>
                </div>
            </div>
        </div>
        <button type="submit" class="btn" :disabled="!isValid">Send</button>
    </form> 
</template>

<script>
import M from 'materialize-css'
import axios from 'axios'  
    export default {
        name: 'Form',
        data(){
            return {
                startDate: null,
                endDate: null,
                select: null,
                checkbox: [],
                loader: false,
                isValid: true
            }
        },
        methods: {
            submitHandler(){
                const data = this.getFormData()
                if(data){
                    // request()
                    console.log(data)
                    this.loader = true
                    setTimeout(() => this.loader = false, 3000)
                }
            },
            getFormData(){
                return {
                    start: new Date(this.startDate.date).getTime(),
                    end: new Date(this.endDate.date).getTime(),
                    select: this.select.getSelectedValues(),
                    checkbox: this.checkbox
                }
                // validation
                
            },
            request(url, method, data){
                axios({
                    url: url,
                    method: method,
                    data: data
                }).then(response => {
                    console.log(response)
                }).catch(err => {
                    console.log(err)
                })
            }

        },
        mounted(){
            this.select = M.FormSelect.init(this.$refs.select)
            this.startDate = M.Datepicker.init(this.$refs.start, {
                autoClose: true,
                defaultDate: new Date,
                setDefaultDate: true,
                format: 'mm-dd-yyyy'
            });
            this.endDate = M.Datepicker.init(this.$refs.end, {
                autoClose: true,
                format: 'mm-dd-yyyy'
            });
        }
    }
</script>

<style>
    .form .date-active {
        transform: translateY(-14px) scale(0.8);
        transform-origin: 0 0;
    }
</style>